    // 点击选中  
    check(item, index) {
      for (var j in this.invoices) {
        this.invoices[j].checked = false;
        this.invoices[index].checked = true;
      }
      this.invCode = item.invCode;
      this.invNumber = item.invNumber;
      console.log(this.invCode, this.invNumber)

    },
    // 类型排序
