            } else if (this.invoices[i].invType == '3') {
              this.invoices[i].invType = '电子发票'
            } else if (this.invoices[i].invType == '4') {
              this.invoices[i].invType = '机动车发票'
            } else {
              this.invoices[i].invType = '改类型发票未知'
            }
          }
