<template>
  <div>
    <header>
      <span onclick="window.history.go(-1)"><img src="../assets/images/fanhui.png" alt=""></span>

      <p>报销</p>
    </header>

    <!-- 查询结果显示 -->
    <div class="con">
      <div class="voiceList">
        <p>报销单号:
          <span>2424324</span>
        </p>
        <p>报销总额:
          <span style="color:#0099FF">¥600</span>
        </p>

      </div>
      <div class="voiceList">
        <p>餐饮费:
          <span style="color:#0099FF">¥100</span>
        </p>
        <p>发票号码:
          <span>{{$route.params.invNumber}}</span>
          <i>检验一致</i>
        </p>
        <p>发票日期:
          <span>24324234</span>
          <i>报销审核通过</i>
        </p>
        <p>开票方:
          <span>几点发说链接发链接</span>
        </p>

      </div>

    </div>
    <div class="btn">
      <el-button type="primary" @click="ok()">提交</el-button>

    </div>

  </div>
</template>
<script>


export default {
  name: "myExpense",
  methods: {

    ok() {
      var strJson = {
        voucher: 'dZDstbiufOy4DZdDvp5bXqBHswuXUYteo4DqfVJj8eo=',
        invCode: '3100171130',
        invNumber: '32669449'
      }
      var str = JSON.stringify(strJson)

      var base64Data = this.Base64.encode(str);

      console.log(base64Data)

      this.axios({
        method: 'post',
        url: `${this.siteUrl}/MDInvReimburse`,
        data: base64Data
      })
        .then((response) => {
          // this.invoices = response.data.invoices;
          console.log(response)
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  

},
mounted: function() {
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
header span {
  position: absolute;
}

header span img {
  height: 20px;
  margin-left: 10px;
  margin-top: 15px;
}


.con {
  background: #eee;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin-bottom: 50px;
}

.con .voiceList {
  background: #fff;
  margin-top: 10px;
  margin-bottom: 10px;
}

.con .voiceList p {
  height: 30px;
  line-height: 30px;
  border-bottom: 1px solid #eee;
  padding-left: 15px;
  color: #333;
  font-size: 14px;
  position: relative;
}

.con .voiceList i {
  position: absolute;
  font-style: normal;
  right: 10px;
  font-size: 12px;
  color: #3B9E8C;
}

.con .voiceList span {
  padding-left: 15px
}

.btn {
  width: 100vw;
  text-align: center;
  margin-top: 50px;
}
</style>



