import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import InputInfo from '@/components/inputInfo'
import MyInVoice from '@/components/myInVoice'
import InVoiceCheck from '@/components/inVoiceCheck'
import MyExpense from '@/components/myExpense'
import ExpenseDetail from '@/components/expenseDetail'
import InVoiceDetail from '@/components/inVoiceDetail'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/inputInfo',
      name: 'inputInfo',
      component: InputInfo
    },
    {
      path: '/myInVoice',
      name: 'myInVoice',
      component: MyInVoice
    },
    {
      path: '/inVoiceCheck',
      name: 'inVoiceCheck',
      component: InVoiceCheck
    },
    {
      path: '/myExpense',
      name: 'myExpense',
      component: MyExpense
    },
    {
      path: '/expenseDetail',
      name: 'expenseDetail',
      component: ExpenseDetail
    },
    {
      path: '/inVoiceDetail',
      name: 'inVoiceDetail',
      component: InVoiceDetail
    },

    
    
  ]
})



